const cssRuleList = Array
  .from(document.styleSheets)
  .reduce((prev, curr) => {
    // @ts-ignore
    prev = prev.concat(Array.from(curr.cssRules));

    return prev;
  }, []);

const styleText = cssRuleList
  .map((cssStyleRule: any) => {
    if (cssStyleRule.cssText.includes('nzu-')) {
      return cssStyleRule.cssText;
    }

    return '';
  })
  .join('');

console.log('styleText:', styleText);

interface MintingWidgetInterface {
  mount: () => void;
  destroy: () => void;
  open: () => void;
  close: () => void;
}

export class MintingWidget implements MintingWidgetInterface {
  #element = null as any as HTMLElement | null;

  #name = MintingWidgetComponent.name

  #visibilityStateFlag = {
    isOpen: 'true',
    isClose: 'false',
  }

  // constructor() {
  //   //
  // }

  mount: MintingWidgetInterface['mount'] = () => {
    const Component = defineCustomElement(MintingWidgetComponent);
    customElements.define(this.#name, Component);

    this.#element = document.createElement(this.#name);
    document.body.append(this.#element);
    this.#element.setAttribute('is-opened', this.#visibilityStateFlag.isClose);

    const style = document.createElement('style');
    style.textContent = styleText;
    this.#element.shadowRoot?.appendChild(style);
  };

  destroy: MintingWidgetInterface['destroy'] = () => {
    this.#throwErrorIfNotMounted();

    document.body.removeChild(this.#element!);
  };

  open: MintingWidgetInterface['open'] = () => {
    this.#throwErrorIfNotMounted();
    // @ts-ignore
    // eslint-disable-next-line
    // console.log(111, this.#element._instance.setupState.open);
    // @ts-ignore
    // eslint-disable-next-line
    // this.#element._instance.setupState.open();
    console.log({ ...this.#element });
    console.log({ shadowRoot: document.querySelector(this.#name)?.shadowRoot });

    this.#element!.setAttribute('is-opened', this.#visibilityStateFlag.isOpen);
  };

  close: MintingWidgetInterface['close'] = () => {
    this.#throwErrorIfNotMounted();

    this.#element!.setAttribute('is-opened', this.#visibilityStateFlag.isClose);
  };

  #throwErrorIfNotMounted() {
    if (this.#element == null) {
      throw new Error('The widget cannot be used if it is not mounted!');
    }
  }
}

// @ts-ignore
window.MintingWidget = MintingWidget;
