import type { ComponentPropsOptions, ExtractPropTypes } from 'vue';

type MintingWidgetPropsSchema = {
  isOpened?: string,
}

export const props: ComponentPropsOptions<MintingWidgetPropsSchema> = {
  // isOpened: {
  //   type: Boolean,
  //   required: false,
  //   default: false,
  // },
  isOpened: {
    type: String,
    required: false,
    default: 'true',
    validator(value: any) {
      return ['false', 'true'].includes(value);
    },
  },
};

export type MintingWidgetProps = ExtractPropTypes<typeof props>
