import type { ComponentPropsOptions, PropType } from 'vue';

export const propsRecord = {
  modelValue: 'modelValue',
  modelModifiers: 'modelModifiers',
  min: 'min',
  max: 'max',
} as const;

type ModelModifier<T extends string = string> = Record<T, true | undefined>

export type InputNumberPropsSchema = {
  [propsRecord.modelValue]?: number;
  [propsRecord.modelModifiers]?: ModelModifier;
  [propsRecord.min]?: number;
  [propsRecord.max]?: number;
}

type InputNumberProps = ComponentPropsOptions<InputNumberPropsSchema>

export const props: InputNumberProps = {
  [propsRecord.modelValue]: {
    type: Number,
    required: false,
    default: 0,
  },

  [propsRecord.modelModifiers]: {
    type: Object as PropType<ModelModifier>,
    required: false,
    default: () => ({}),
  },

  [propsRecord.min]: {
    type: Number,
    required: false,
    default: -Infinity,
  },

  [propsRecord.max]: {
    type: Number,
    required: false,
    default: Infinity,
  },
};
