import { defineComponent, shallowRef } from 'vue';
import type { Events } from 'vue';
import MintImage from './components/mint-image/mint-image.component.vue';
import MintDescription from './components/mint-description/mint-description.component.vue';
import InputNumber from './components/input-number/input-number.component.vue';
import TwoColumnSection from './components/two-column-section/two-column-section.component.vue';

const MintingForm = defineComponent({
  inheritAttrs: false,

  name: 'minting-form',

  components: {
    MintImage,
    MintDescription,
    InputNumber,
    TwoColumnSection,
  },

  setup() {
    const minMintValuePerTime = 1;
    const currentNftAmountToMint = shallowRef(minMintValuePerTime);
    const maxMintValuePerTime = 10;

    const onSubmit = (event: Events['onSubmit']): void => {
      console.log('"onSubmit" event:', event);
      console.log('currentNftAmountToMint:', currentNftAmountToMint);
    };

    return {
      minMintValuePerTime,
      maxMintValuePerTime,
      onSubmit,
      currentNftAmountToMint,
    };
  },
});

export default MintingForm;
