import { defineComponent, shallowRef, watch } from 'vue';
import type { Events } from 'vue';
import { props } from './input-number.props';
import { emits } from './input-number.emits';

const InputNumber = defineComponent({
  inheritAttrs: false,

  name: 'input-number',

  props,

  emits,

  setup(prop: any, context) {
    const isDecrementButtonDisabled = shallowRef(false);
    const isIncrementButtonDisabled = shallowRef(false);

    const inputNumber = shallowRef(+prop.modelValue || 0);

    const updateModelValue = (value: number) : void => {
      context.emit('update:modelValue', value);
    };

    const decrement = (event: Events['onClick']): void => {
      console.log(event);

      if (inputNumber.value > prop.min) {
        inputNumber.value -= 1;
      }

      updateModelValue(inputNumber.value);
    };

    const increment = (event: Events['onClick']): void => {
      console.log(event);

      if (inputNumber.value < prop.max) {
        inputNumber.value += 1;
      }

      updateModelValue(inputNumber.value);
    };

    watch(
      () => inputNumber.value,
      (value) => {
        if (value <= prop.min) {
          isDecrementButtonDisabled.value = true;
        } else {
          isDecrementButtonDisabled.value = false;
        }

        if (value >= prop.max) {
          isIncrementButtonDisabled.value = true;
        } else {
          isIncrementButtonDisabled.value = false;
        }
      },
      {
        immediate: true,
      },
    );

    return {
      decrement,
      increment,
      inputNumber,
      isDecrementButtonDisabled,
      isIncrementButtonDisabled,
    };
  },
});

export default InputNumber;
