import { defineComponent, shallowRef } from 'vue';
import { props } from './darkened-overlay.props';

const DarkenedOverlay = defineComponent({
  inheritAttrs: false,

  name: 'darkened-overlay',

  props,

  setup(prop: any, context) {
    const isVisible = shallowRef<boolean>(prop.isVisible);

    const hide = (): void => {
      isVisible.value = true;
    };

    const bindings = {
      hide,
      isVisible,
    };

    const exposes = {};

    context.expose(exposes);

    return bindings;
  },
});

export default DarkenedOverlay;
