import { propsRecord } from './input-number.props';
import type { InputNumberPropsSchema } from './input-number.props';

export const emitNameRecord = {
  updateModelValue: `update:${propsRecord.modelValue}`,
} as const;

type InputNumberEmits = {
  [emitNameRecord.updateModelValue]:
    (value: NonNullable<InputNumberPropsSchema['modelValue']>) => boolean
};

export const emits: InputNumberEmits = {
  [emitNameRecord.updateModelValue]: (value) => {
    if (typeof value === 'number') return true;

    return false;
  },
};
