import { shallowRef } from 'vue';

export const useMintingWidgetStore = () => {
  const isOpened = shallowRef<'true' | 'false'>('false');

  const open = (): void => {
    isOpened.value = 'true';
  };

  const close = (): void => {
    isOpened.value = 'false';
  };

  return {
    isOpened,
    open,
    close,
  };
};
